.BackgroundWrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: -1;

  .BackgroundImage {
    position: absolute;
    min-height: 100%;
    width: 100%;
    aspect-ratio: auto;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}